import React, { useEffect, useState } from 'react';
import { useIdleTimer } from 'react-idle-timer';
import classNames from 'classnames';
import useBKlog from '../hooks/useBKlog';
import Block from '../components/bklog/block';

import { BlockData } from '../types/bklog';

import { TEST_CASE_01 } from '../index.test';
import Sibebar from '../components/sidebar';
import usePage from '../hooks/usePage';

function BklogContainer() { 
  
  const [count, setCount] = useState(1);
  
  const { 
    state, 
    initBlock, 
    onAddBlock, 
    onCommitBlock, 
    onChangeTextStyle,
    onSwitchBlock,
    onRevertBlock
  } = useBKlog();


  const click = () => {
    onAddBlock(undefined, undefined, {
      index: 0,
      id: "d5cc2725-97ec-494b-bc80-c16f96379e61",
      type: "text",
      parentBlockId: null,
      preBlockId: null,
      nextBlockId: null,
      property: {
        type:  "BKlog-h1",
        styles: {
          color: null,
          backgroundColor: null
        },
        contents: [
          [" 1."],
          ["", [["b"], ["fc", "#c0f"], ["bc", "#000"], ["i"],["a", "https://www.naver.com"]]],
          [" ", [["i"]]],
          ["", [["b"]]],
          [" .", [["fc", "#f00"]]]
        ]
      },
      children: [
        "2453d55f-66f0-4ad0-b796-0dbcd8d82ce8"
      ]
    });
  }

  const handleOnIdle = () => {
    console.log('last active', getLastActiveTime());
    if(state.stage[0]) {
      console.log('commit');
      onCommitBlock();
    }
  }
  const handleClick = () => {
    onChangeTextStyle (1, ["bc", "#fc0"], 2, 10, "color");
  }

  const testHandleClick = () => {
    onSwitchBlock(state.blocks[3].id, state.blocks[7].id, false);
    setCount(count+1);
  }

  const revertHandleClick = () => {
    onRevertBlock();
  }
  // const handleOnActive = (event: any) => {
  //   console.log('user is active', event)
  //   console.log('time remaining', getRemainingTime())
  // }

  // const handleOnAction = (e: any) => {
  //   console.log('user did something', e)
  // }

  const { getLastActiveTime } = useIdleTimer({
    timeout: 10 * 60 * 5,
    onIdle: handleOnIdle,
    debounce: 500
  })

  const blockData:any = initBlock[0]? initBlock : null;

  // useEffect(() => {
  //   console.log("case: blockList ", TEST_CASE_01);
  //   console.log("adsd");
  // }, []);

  const { pageToggle } = usePage();

  return (
    <div className="flex h-full relative overflow-auto">
      <Sibebar />
      <div className={classNames(
        "flex-auto w-full h-full py-2 pr-2 ml-2"
      )}>
        <div className="blocklog items-center overflow-auto w-full notranslate text-gray-700 bg-white h-full rounded-md shadow-md">
          <div className="cover mb-8"></div>
          <div className="m-auto h-full block-container">
            {
              blockData?
              blockData.map((block: BlockData<any>)=> 
                <Block 
                  blockData={block}
                  key={block.id}
                />
              ) : <div>""</div>
            }
            {
              blockData.length <= 1? 
              <div className="bk-zone"> 
                <div 
                  className="bk-block" 
                  placeholder="..."
                  onFocus={()=>onAddBlock()}
                  onClick={()=>onAddBlock()}
                  contentEditable="true"
                ></div>
              </div> : null
            }
            <button onClick={click}> </button>
            <button onClick={handleClick}> </button>
            <button onClick={testHandleClick}></button>
            <button onClick={revertHandleClick}></button>
          </div>
        </div>
      </div>
    </div>
    
  )
}

export default BklogContainer;